	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/10/antifasuchegr.jpg\" onclick=\"javascript:openPopup('/images/fotos/antifasuchegr.jpg',434,614);return false;\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/10/antifasuche1.jpg\" width=\"200\" height=\"291\" border=\"0\" hspace=\"4\" vspace=\"4\" align=\"right\" alt=\"Die Berliner AntiFa bittet um ihre Mithilfe\" title=\"Click to open in new window\" /></a> The Berlin Police does not want you to see this graphic, a parody of a 'wanted' poster depicting alledged acts of police brutality on May Day. As reported by <a href=\"http://www.schockwellenreiter.de/2002/10/18.html#a8578\">Der Schockwellenreiter</a> and <a href=\"http://www.heise.de/newsticker/data/wst-18.10.02-003/\">Heise</a>, the police threatened to shut down the website of the creators of the parody, the Anitfascist Action Berlin, unless the posters was pulled from their website. The police have now started issuing similar threats to sites mirroring the poster, and Der Schockwellenreiter has called on bloggers everywhere to mirror the poster to render the censorship efforts absurd. Now, I find the poster in questionable taste, as well as the whole 'tradition' of May Day rioting, but I should be able to view the poster myself and make my own decision.</p>","fields":{"title":"Berlin Polices The Internet","date":"21 Oct 2002","url":"https://papascott.de/2002-10-21-berlin-polices-the-internet/"}}},"pathContext":{"slug":"/2002-10-21-berlin-polices-the-internet/","prev":{"fields":{"title":"The New Old Blogroll","date":"20 Oct 2002","slug":"/2002-10-20-the-new-old-blogroll/"}},"next":{"fields":{"title":"How to redirect an RSS feed","date":"21 Oct 2002","slug":"/2002-10-21-how-to-redirect-an-rss-feed/"}}}}

